import data from '../../data/index';

export default function quests(state = [ data.quests[0] ], action) {
    switch(action.type) {
        case 'ADD_QUEST':
            const find_quest = data.quests.find(f => f.id === action.data)
            return state.concat(find_quest);
        case 'REMOVE_QUEST':
            return state.filter(f => f.id !== action.data);
        default:
            return state;
    }
}