import data from '../../data/index';

export default function effects(state = [ data.effects[0] ], action) {
    switch(action.type) {
        case 'ADD_EFFECT':
            const find_effect = data.effects.find(f => f.id === action.data)
            return state.concat(find_effect);
        case 'REMOVE_EFFECT':
            return state.filter(f => f.id !== action.data);
        default:
            return state;
    }
}