import data from '../../data/index';
const { items } = data;

export default function equipment(state = [
    items[0],
    items[3],
    items[4],
    items[5],
    items[6],
    items[7]
], action) {
    switch(action.type) {
        case 'ADD_ITEM':
            const add = items.find(f => f.id === action.data);
            const check = state.find(f => f.id === action.data);
            if (check) {
                const x = [...state];
                x.map(m => {
                    if (m.id === add.id) {
                        m.quantity += add.quantity;
                        return m;
                    }
                    return m;
                })
                return state = x;
            } else {
                return state.concat(add);
            }
        case 'REMOVE_ITEM':
            const remove = state.find(f => f.id === action.data);
            if (remove.quantity === 1) {
                return state.filter(f => f.id !== remove.id);
            } else {
                const x = [...state];
                x.map(m => {
                    if (m === remove) {
                        m.quantity -= 1;
                        return m;
                    }
                    return m;
                })
                return state = x;
            }
        default:
            return state;
    }
}