export default function stats(state = {
    stats: {
        str: 10,
        dex: 15,
        con: 14,
        int: 16,
        wis: 12,
        cha: 8
    },
    rolls: {
        str: 0,
        dex: 2,
        con: 2,
        int: 5,
        wis: 3,
        cha: -1
    },
    skills: {
        acrobatic: 2,
        athetic: 0,
        history: 3,
        intuition: 3,
        medicine: 1,
        animals: 1,
        deception: -1,
        perception: 3,
        persuasion: -1,
        nature: 3,
        religion: 5,
        stealth: 2,
        survival: 1,
        investigation: 5,
        arcane: 5,
        performance: -1,
        intimidation: -1,
        robery: 2
    }
}, action) {
    switch(action) {
        case 'none_for_now':
            return state;
        default:
            return state;
    }
}