import data from '../../data/index';

export default function info(state={}, action) {
    switch(action.type) {
        case 'NEW_INFO_QUEST':
            return state = {type: 'quest', info: data.quests[action.data - 1]};
        case 'NEW_INFO_EFFECT':
            return state = {type: 'effect', info: data.effects[action.data - 1]};
        case 'NEW_INFO_ITEM':
            return state = {type: 'item', info: data.items[action.data - 1]};
        case 'NEW_INFO_EXP':
            return state = {type: 'exp', info: action.data};
        default:
            return state;
    }
}